import React, { useMemo } from 'react';
import format from 'date-fns/format';
import { CardHeader, CardContent, Avatar, Typography, makeStyles, Paper, Theme, Box } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';
import { Post } from 'src/models';
import RouteLink from 'components/Utils/RouteLink';

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  root: {
    padding: spacing(2),
  },
  title: {
    fontWeight: 'bold',
    lineHeight: 1.3,
  },
  link: {
    textDecoration: 'none',
  },
  contents: {
    marginBottom: spacing(2),
  },
  avatar: {
    width: 35,
    height: 35,
  },
  profileImage: {
    marginRight: spacing(1),
  },
  publishedDate: {
    fontSize: 12,
  },
}));

interface Props {
  post: Post;
  showDescription?: boolean;
}
const PostCard = ({ post: { createdAt, link, author, title, excerpt }, showDescription = true }: Props) => {
  const publishedDate = useMemo(() => {
    return format(new Date(createdAt), 'yyyy-MM-dd');
  }, [createdAt]);

  const styles = useStyles();

  return (
    <AnimatedListItem>
      <RouteLink to={link} className={styles.link}>
        <Paper className={styles.root}>
          <Box className={styles.contents}>
            <Typography gutterBottom={true} variant="subtitle1" component="h3" className={styles.title}>
              {title}
            </Typography>
            {showDescription && <Typography variant="caption">{excerpt}</Typography>}
          </Box>
          <Box display="flex" alignItems="center">
            <Box className={styles.profileImage}>
              <Avatar src={author.profileImage} alt={author.name} className={styles.avatar} />
            </Box>
            <Box>
              <Typography variant="caption">{author.name}</Typography>
              <Typography color="textSecondary" className={styles.publishedDate}>
                {publishedDate}
              </Typography>
            </Box>
          </Box>
        </Paper>
      </RouteLink>
    </AnimatedListItem>
  );
};

export default PostCard;
